
.auth0-lock-header-bg {
  background-color: #f0f0f0 !important;
  opacity: 1 !important;
  background-image: none !important;
}

.auth0-lock-header-bg-blur {
  background-image: none !important;
}

.auth0-lock-custom-icon {
  width: 14px !important;
  height: 14px !important;
  filter: invert(.6) !important;
}

.auth0-lock-badge-bottom {
  display: none !important;
}

.auth0-lock.auth0-lock.auth0-lock-opened .auth0-lock-overlay {
  opacity: 0.6 !important;
}

.auth0-lock-widget {
  box-shadow: 0 0 40px 4px #11111881 !important;
}