.snackbar-notification {
    background-color: white;
    border: 1px solid white;
}

.snackbar-type-success {
    background-color: #f6ffed;
    border-color: #b7eb8f;
}

.snackbar-type-info {
    background-color: #e6f7ff;
    border-color: #91d5ff
}

.snackbar-type-warn {
    background-color: #fffbe6;
    border-color: #ffe58f;
}

.snackbar-type-error {
    background-color: #fff1f0;
    border-color: #ffa39e;
}
